.header {
  display: block;
  padding: $padding-bt__lg $padding-lr__lg 0 $padding-lr__lg; }

.header__logo-name {
  color: transparent;
  user-select: none;
  cursor: default; }

.header__logo-svg {
  float: left; }

.header__row {
  list-style: none;
  text-align: right; }

.header__list {
  display: inline-block;
  margin-left: $padding-lr__lg;
  a {
    font-size: 160%;
    color: #9F9F9F;
    letter-spacing: 0.2em;
    text-decoration: none;
    transition: all 0.25s ease-in-out 0.001s;
    &:hover {
      color: $color__light; }
    &:hover > .header__list--underline {
      width: 100%;
      background-color: $color__light; } } }

.header__list--underline {
  background-color: #7A7A7A;
  margin-top: 0.1em;
  height: 1px;
  width: 0;
  transition: all 0.25s ease-in-out; }


