  width: 0;
  transition: all 0.25s ease-in-out; }

.main-slogan {
  display: block;
  padding-top: 40px; }

.main-slogan__wrapper {
  cursor: default;
  user-select: none;
  padding-left: 120px;
  max-width: 600px;
  font-size: 2.4rem;
  letter-spacing: 0.18em; }

.main-hero {
  display: block;
  margin-top: 160px;
  background-color: #F4F4F4; }
  .main-hero:after {
    content: "";
    display: table;
    clear: both; }

.main-hero__left-hand {
  margin-top: -8%;
  margin-left: -8%;
