  float: left;
  max-width: 38%;
  transition: transform 1s ease-in-out, margin 1s ease-in-out; }
  .main-hero__left-hand img {
    width: 100%;
    height: 100%; }

.main-hero__left-hand--move {
  transform: rotate(-5deg);
  margin-top: -4%;
  margin-left: -2%; }

.main-hero__right-hand {
  margin-top: -18%;
  margin-right: -10%;
  transform: rotate(-1deg);
  float: right;
  max-width: 65%;
  transition: transform 1s ease-in-out, margin 1s ease-in-out; }
  .main-hero__right-hand img {
    width: 100%;
    height: 100%; }

.main-hero__right-hand--move {
  transform: rotate(5deg);
  margin-top: -7%;
  margin-right: -6%; }

.main-skills {
  background-color: #F4F4F4;
  color: #151515;
  display: block; }

.main-skills__title {
