  font-size: 3rem;
  margin-bottom: 20px;
  padding: 0 60px 40px 60px; }

.main-skills__grid {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding-left: 0; }

.main-skills__item {
  min-width: 120px;
  min-height: 120px;
  margin: 0 90px 40px 90px;
  font-size: 6rem; }

.main-skills__svg--fill {
  fill: #151515;
  transition: all 0.25s ease-in-out; }

.main-work {
  display: block;
  padding-bottom: 40px; }

.main-work__title {
  font-size: 3rem;
  padding: 0 60px 0 60px; }

.main-work__grid {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 0; }

.main-work__item {
  transition: all 0.5s ease-in-out 0.3s; }

.main-work__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  outline: solid #151515 4px; }

.main-about {
  padding: 0 60px 40px 60px;
  display: flex;
  justify-content: space-between; }
  .main-about > * {
    flex: 1;
    margin: 0 16px; }
  .main-about h3 {
    font-size: 2rem; }
  .main-about p {
    font-family: arial light;
    font-size: 1.2rem;
    line-height: 1.5;
    hyphens: auto;
    text-align: justify; }

.main-about__form {
  font-family: arial light;
  height: 260px;
  justify-content: space-between;
  display: flex;
  flex-direction: column; }
  .main-about__form > * {
    background-color: #151515;
    color: #F4F4F4;
    padding: 0 8px;
    border-radius: 3px;
    margin: 8px 0;
    border: solid #F4F4F4 2px; }
  .main-about__form .field-input {
    flex: 1;
    font-size: 0.8rem; }
  .main-about__form .send-btn {
    cursor: pointer;
    letter-spacing: 0.1rem;
    flex: 1.4;
    width: 25%;
    color: #F4F4F4;
    font-weight: bold;
    border: none;
    border: 1px solid #F4F4F4;
    transition: all 0.1s ease-in-out; }
    .main-about__form .send-btn:hover {
      background-color: #F4F4F4;
      color: #151515; }
  .main-about__form .textarea-input {
    padding-top: 4px;
    font-size: 0.8rem;
    flex: 3.8; }

.main-about__icon-row {
  padding: 0;
  list-style: none; }

.fa {
  font-size: 36px;
  color: #F4F4F4;
  transition: color 0.1s ease-in-out; }
  .fa:hover {
    color: #9F9F9F; }

.main-about__icon-list {
  margin: 0;
  padding: 0 20px 0 0;
  float: left; }

.footer {
  width: 100%; }
  .footer p {
    padding: 0 60px 10px 0;
    text-align: end;
    font: menu;
    color: #F4F4F4;
    font-size: 0.8rem; }

.footer-fixed-bg {
  height: 20vh;
  background-image: url(../img/map01.png);
  background-repeat: no-repeat;
  background-position: center;
  background-color: #151515;
  background-size: cover;
  background-attachment: fixed; }

@media screen and (max-width: 720px) {
