.main-hero {
  display: block;
  margin-top: 160px;
  background-color: $color__light;
  &:after {
    content: "";
    display: table;
    clear: both; } }

.main-hero__left-hand {
  margin-top: -8%;
  margin-left: -8%;
  transform: rotate(5deg);
  float: left;
  max-width: 38%;
  transition: transform 1s ease-in-out, margin 1s ease-in-out;
  img {
    width: 100%;
    height: 100%; } }

.main-hero__left-hand--move {
  transform: rotate(-5deg);
  margin-top: -4%;
  margin-left: -2%; }

.main-hero__right-hand {
  margin-top: -18%;
  margin-right: -10%;
  transform: rotate(-1deg);
  float: right;
  max-width: 65%;
  transition: transform 1s ease-in-out, margin 1s ease-in-out;
  img {
    width: 100%;
    height: 100%; } }

.main-hero__right-hand--move {
  transform: rotate(5deg);
  margin-top: -7%;
  margin-right: -6%; }
