.main-work {
  display: block;
  padding-bottom: $padding-bt__lg; }

.main-work__title {
  font-size: 3rem;
  padding: 0 $padding-lr__lg 0 $padding-lr__lg; }

.main-work__grid {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding-left: 0; }

.main-work__item {
  transition: all 0.5s ease-in-out 0.3s; }

.main-work__img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  outline: solid $color__dark 4px; }
